import React from "react";
import CharacterComponent from "./CharacterComponent";
import all from "./all";
import CharacterPage from "./CharacterPage/CharacterPage";
import "./Characters.css";
// import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
// import CharacterPage from "./CharacterPage/CharacterPage";

class Characters extends React.Component {
  constructor() {
    super();
    this.state = {
      characters: all,
    };
    // this.render = this.render.bind(this);
  }
  render() {
    const characterComps = this.state.characters.map((data) => {
      return (
        <div className="character-compo">
          <CharacterComponent data={data} />
        </div>
      );
    });
    return (
      <div className="App">
        <div className="top-most-heading-div">
          <p>A Guide to</p>
          <h1 className="top-most-heading">Star Wars</h1>
        </div>
        <div className="character-compo-part">{characterComps}</div>
      </div>
    );
  }
}

export default Characters;
