import "./App.css";
import React from "react";
import all from "./Components/all";
import Home from "./Components/Home";

// import CharacterPage from "./Components/CharacterPage/CharacterPage.js";
import Characters from "./Components/Characters";
import CharacterPage from "./Components/CharacterPage/CharacterPage.js";
import { BrowserRouter as Router, Link, Route, Switch } from "react-router-dom";
class App extends React.Component {
  render() {
    const routingCompos = all.map((data) => {
      return (
        <Route path={"/characters/" + data.id}>
          {" "}
          <CharacterPage id={data.id} />
        </Route>
      );
    });
    return (
      <React.Fragment>
        <div>
          <Router>
            <Switch>
              <Route exact path="/">
                <Home />
              </Route>
              <Route exact path="/characters/">
                <Characters />
              </Route>
              <Route path={"/Characters/1"}>
                {" "}
                <CharacterPage id="1" />
              </Route>
              <Route
                path="/characters/:id"
                children={<CharacterPage />}
              ></Route>
            </Switch>
          </Router>
        </div>
      </React.Fragment>
    );
  }
}

export default App;
