import React from "react";

class DetailsPage extends React.Component {
  constructor() {
    super();
    this.state = {
      isLoaded: false,
      homePlanet: [],
      imageURL: "./Images/blank-profile-image.webp",
      affiliations: [],
    };
  }
  componentDidMount() {
    if (this.props.data.image) {
      this.setState({ isLoaded: true, imageURL: this.props.data.image });
    }
    if (this.props.data.affiliations) {
      this.setState({ affiliations: this.props.data.affiliations });
    }
    // setTimeout(() => {
    //   if (this.props.data.image) {
    //     this.setState({ isLoaded: true, imageURL: this.props.data.image });
    //   }
    // }, 50);
    // setTimeout(() => {
    //   if (this.props.data.affiliations) {
    //     this.setState({ affiliations: this.props.data.affiliations });
    //   }
    // }, 50);
  }
  render() {
    // if (this.props.data.image) {
    //   this.setState({ isLoaded: true, imageURL: this.props.data.image });
    // }
    // if (this.props.data.affiliations) {
    //   this.setState({ affiliations: this.props.data.affiliations });
    // }
    const Affiliations = this.state.affiliations.map((data) => {
      return <li>{data}</li>;
    });
    return (
      <React.Fragment>
        <div className="profile-image">
          <img
            src={this.state.imageURL}
            alt="Profile"
            onError={() => (this.src = "./Images/blank-profile-image.webp")} // check it
          />
        </div>
        <div className="information">
          <ul>
            <li className="character-name">
              {this.props.data.name}
              <span
                style={{
                  display: this.props.data.model ? "block" : "none",
                }}
              >
                ({this.props.data.model})
              </span>
            </li>
            <li>Home World: {this.props.data.homeworld}</li>
            <li>Species: {this.props.data.species}</li>
            <li>Height: {this.props.data.height}</li>
            <li>Mass: {this.props.data.mass}</li>
            <li>Birth Year: {this.props.data.born}</li>
            <li
              style={{
                display: this.props.data.bornLocation ? "block" : "none",
              }}
            >
              Birth Location: {this.props.data.bornLocation}
            </li>
            <li
              style={{
                display: this.props.data.manufacturer ? "block" : "none",
              }}
            >
              Manfacturer: {this.props.data.manufacturer}
            </li>
            <li>Death Year: {this.props.data.died}</li>
            <li>Died Location: {this.props.data.diedLocation}</li>
            <li>Gender: {this.props.data.gender}</li>
            <li>Hair Color: {this.props.data.hairColor}</li>
            <li>Eye Color: {this.props.data.eyeColor}</li>
            <li>Skin Color: {this.props.data.skinColor}</li>
            <li
              style={{
                display: this.props.data.cybernetics ? "block" : "none",
              }}
            >
              Cybernetics: {this.props.data.cybernetics}
            </li>
            <li
              style={{
                display: this.props.data.affiliations ? "block" : "none",
              }}
            >
              Affiliations:<ol>{Affiliations}</ol>
            </li>
          </ul>
        </div>
      </React.Fragment>
    );
  }
}

export default DetailsPage;
