function animation() {
  const style = {};
  return (
    <div class="starwars-demo" style={{}}>
      <img
        src="//cssanimation.rocks/demo/starwars/images/star.svg"
        alt="Star"
        class="star"
      />
      <img
        src="//cssanimation.rocks/demo/starwars/images/wars.svg"
        alt="Wars"
        class="wars"
      />
      <h2 class="byline" id="byline">
        The Force Awakens
      </h2>
    </div>
  );
}

function CharacterPage(props) {
  const { id } = useParams();
  const num = id.replace(":", "");
  const [isLoaded, setisLoaded] = useState(false);

  // window.prompt(num < 17 && num !== 17 ? num - 1 : num - 2);
  useEffect(() => {
    setInterval(() => setisLoaded(true), 2000);
  }, []);
  if (isLoaded) {
    return (
      <div className="character-page-complete">
        <div className="top-most-heading-div">
          <p>A Guide to</p>
          <h1 className="top-most-heading">Star Wars</h1>
        </div>
        <div className="details-part">
          <DetailsPage data={all[num < 17 && num !== 17 ? num - 1 : num - 2]} />
        </div>
      </div>
    );
  } else {
    return (
      <div class="starwars-demo" style={{}}>
        <img
          src="//cssanimation.rocks/demo/starwars/images/star.svg"
          alt="Star"
          class="star"
        />
        <img
          src="//cssanimation.rocks/demo/starwars/images/wars.svg"
          alt="Wars"
          class="wars"
        />
        <h2 class="byline" id="byline">
          Loading....
        </h2>
      </div>
    );
  }
}
export default CharacterPage;

