import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import CharacterPage from "./CharacterPage/CharacterPage";

class CharacterComponent extends React.Component {
  constructor() {
    super();
    this.state = {};
  }
  render() {
    const style = { backgroundImage: this.props.data.image };
    return (
      <div
        key={this.props.data.id}
        onClick={() => {
          window.location = "/Characters/:" + this.props.data.id;
        }}
      >
        <button
          style={{ backgroundImage: "url(" + this.props.data.image + ")" }}
          className="characterpage-each-character-button"
        ></button>
        <h3 className="characters-name">{this.props.data.name}</h3>
        <Router>
          <Route path={"/characters/" + this.props.data.id}>
            <CharacterPage id={this.props.data.id} />
          </Route>
        </Router>
      </div>
    );
  }
}

export default CharacterComponent;
