import React from "react";

class Home extends React.Component {
  render() {
    return (
      <div className="App">
        <div className="top-most-heading-div">
          <p>A Guide to</p>
          <h1 className="top-most-heading">Star Wars</h1>
        </div>
        <div className="homepage-characters-button">
          <button
            className="characters-button"
            onClick={() => {
              window.location = "/characters/";
            }}
          ></button>
          <br></br>
          <span className="characters-name">Characters</span>
        </div>
      </div>
    );
  }
}

export default Home;
